import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'


Vue.use(VueRouter)

const routes = [
  {path: '/', name: 'home', component: HomeView},
  {path:'/signup',name: 'signup',component:()=> import('../views/SignUp')},
  {path:'/get-help',name: 'gethelp',component:()=> import('../views/GetHelp')},
  {path:'/pricing',name: 'pricing',component:()=> import('../views/PricingView')},
  {path: '/our-story', name: 'ourstory', component: () => import('../views/OurStory.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
